.calc-overlay {
    position: fixed;
    padding: 20px;
    max-width: 500px;
    width: 100vw;
    background-color: rgb(207, 207, 207);
    bottom: 0;
    z-index: 1000;
    padding-bottom: 50px;
    box-sizing: border-box;
}

.calc-keys {
    user-select: none;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px 10px;
    /* For larger screens */

}

.calc-btn {
    position: relative;
    background-color: white;
    /* aspect-ratio: 1; */
    color: black;
    font-size: 30px;
    font-weight: 600;
    border-radius: 50px;
    border: none;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.103);
}

.calc-btn.op {
    background-color: rgb(207, 207, 207);
}

.calc-display-container {
    /* display: flex; */
    /* flex-direction: column; */
    width: 100%;
    /* height: 40px; */
    background-color: white;
    padding: 10px 5px;
    margin-bottom: 20px;
    box-sizing: border-box;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.116);
    height: 80px;

}

.calc-display {
    width: 100%;
    border: none;
    font-size: 20px;
    box-sizing: border-box;
}